import { observer } from 'mobx-react';
import { useEffect } from 'react';
import ChartsConfig from '../../config/ChartsConfig';
import ChartStore from '../../stores/ChartStore';
import { MenuBtn } from './Menu';
import icons from '../../images/images';
import './Chart.scss';
import { OptionType } from '../../models/ChartModels';

export const ChartLine = observer(() => {
    const optionsConfig: ChartsConfig = new ChartsConfig();

    let lineOption = optionsConfig.basicOptions[3];
    let icon = ChartStore.iconMap[lineOption.id];
    return (
        <li key={2} onClick={() => {
            ChartStore.setCurrentOption(lineOption.id)
        }} >
            <img className="highcharts-menu-item-btn" src={icon}></img>
            {ChartStore.currentOptionId == lineOption.id && <ul id={`optionMenu${lineOption.id}`}>
                {lineOption.options.map((option, i) => (
                    <li key={i} className="chart-line" title={option.lable} onClick={() => {
                        if (option.type == OptionType.seriesOhlc) {
                            ChartStore.chart.series[0].update({
                                type: 'ohlc'
                            });
                        } else if (option.type == OptionType.seriesLine) {
                            ChartStore.chart.series[0].update({
                                type: 'line',
                                useOhlcData: true
                            });
                        } else if (option.type == OptionType.seriesCandlestick) {
                            ChartStore.chart.series[0].update({
                                type: 'candlestick'
                            });
                        }
                        ChartStore.onTypePicked(lineOption.id, option.icon,option.type);
                    }} >
                        <img className="highcharts-menu-item-btn" src={option.icon}></img>
                        <span className="highcharts-menu-item-title">{option.lable}</span>
                    </li>
                ))}
            </ul>}
        </li>
    );
});