
import { useState } from 'react';
import ChartsConfig from '../../config/ChartsConfig';
import { StatisticType } from '../../services/StatisticService';
import { observer } from 'mobx-react';
import ChartStore from '../../stores/ChartStore';



export const RangeTabs = observer(() =>{
    let config:ChartsConfig = new ChartsConfig();
     return (
        <div className="rangeTabWrapper">
            <div className="taberContainer">
                <div className="tabBar" style={{
                    width: (100 / config.rangeTabs.length) + '%',
                    left: ChartStore.checkedRangePosition
                }}
                ></div>
                <div className="taber">
                    {config.rangeTabs.map((t, i) => {
                        return <div className={ChartStore.rangeTab === t.year ? 'tabItemChecked' : 'tabItem'} onClick={() => {
                            ChartStore.changeRangeTab(t.year, i);
                            return;
                        }}>
                            {t.text}
                        </div>
                    })}
                </div>

            </div>
        </div>
    );
                })