import { Ajax } from '../utils/http';

export class StaticticService {
    private symbol: string;
    constructor(_symbol: string) {
        this.symbol = _symbol;
    }

    public async send(): Promise<StatisticResponse> {
        let response: StatisticResponse;
        let data = {
            symbol: this.symbol,
        }
        try {
            response = await new Ajax<StatisticResponse>('/oliview_data', data).get();
        } catch (e) {
            throw e;
        }
        return response;
    }
}

/*Summary*/
export interface Summary {
    symbol: string;
    name: string;
    date: number;
}

/*Incomes*/
export interface Incomes {
    symbol: string;
    name: string;
    date: number;
    gross_margin: number;
    operating_margin: number;
    net_profit_margin: number;
    return_on_investment: number;
    financial_years: Array<string>;
    total_revenues: Array<number>;
    net_incomes: Array<number>;
}

/*Assets*/
export interface Assets {
    symbol: string;
    name: string;
    date: number;
    quick_ratio: number;
    current_ratio: number;
    long_term_debt_to_equity: number;
    total_debt_to_equity: number;
    financial_years: Array<string>;
    total_assets: Array<number>;
    total_liabs: Array<number>;
}

/*Cash flows*/
export interface CashFlows {
    symbol: string;
    name: string;
    date: number;
    revenue_per_common_share: number;
    operating_cash_flow: number;
    financial_years: Array<string>;
    cashes: Array<number>;
    change_in_cashes: Array<number>;
}

/*Latest_recommendations*/
export interface LatestRecommendations {
    date: string;
    analyst: string;
    rating: number;
    target_price: number;
    firm_name: string;
}



/*Finances*/
export interface Finances {
    incomes: Incomes;
    assets: Assets;
    cash_flows: CashFlows;
}

/*tsModel6*/
export interface StatisticResponse {
    summary: Summary;
    finances: Finances;
    latest_recommendations: LatestRecommendations[];
    strategies: Array<Strategies>;
}


export interface Strategies {
    strategy_name: string;
    begin_date: string;
    end_date: string;
    strategy_results: Array<any>
}

export enum StatisticType {
    incomes = 0,
    assets = 1,
    cashFlows = 2
}