
import { observer } from 'mobx-react';
import ChartStore from '../../stores/ChartStore';
import StatisticStore from '../../stores/StatisticStore';
import './Statistic.scss';
import icons from '../../images/images';
import NoData from '../NoData';
const StrategiesMap: any = {
    "sym_triangle_strategy": { icon: icons.sym_triangle_strategy, name: '对称三角形' },
    "ascending_triangle_strategy": { icon: icons.ascending_triangle_strategy, name: '上升三角形' },
    "descending_triangle_strategy": { icon: icons.descending_triangle_strategy, name: '下降三角形' },
    "flags_strategy": { icon: icons.flags_strategy, name: '通道' },
}

export const LineData = observer(() => {
    return (
        <div className="LineDataWrapper">
            {StatisticStore.strategies.length == 0 ?
                <NoData />
                : <div> <div className="tabTitle">
                    <div className="kline">技术图形</div>
                    <div className="date">日期</div>
                </div>
                    <ul className="list">
                        {StatisticStore.strategies.map((e, i) => {
                            return <li key={i} className="item" style={{ backgroundColor: ChartStore.lineId == i ? "#282828" : "transparent" }}
                                onClick={() => {
                                    ChartStore.addAnnotation(e, i);
                                }}
                            >
                                <div className="icon">
                                    <img src={StrategiesMap[e.strategy_name]['icon']}></img>
                                </div>
                                <div className="title">{StrategiesMap[e.strategy_name]['name']}</div>
                                <div className="date">{`${e.begin_date.replace(/-/g, '/')}-${e.end_date.replace(/-/g, '/')}`}</div>
                            </li>
                        })}

                    </ul></div>}
        </div>
    );
});